export default forwardRef(AboutMe);


const Section = styled.section`
    position: relative;
    z-index: 1;

    & .wrap-1200{
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-flow: row wrap;
    }

    & h2 {
        margin: 50px 0;
        font-weight: 400;
        font-size: 2.2rem;
        text-align: center;
        color: #1d1720;
        flex-basis: 100%;

        & span {
            display: inline-block;
        }
        
        & span::after{
            content: '';
            display: inline-block;
            width: 80%;
            height: 2px;
            margin: 25px auto 0px;
            background: #a2a1dc;
        }
    }

`
const TextWrapper = styled.div`
    display: flex;
    flex-flow: column wrap;
    flex-basis: 45%;
    padding: 10px;
    box-sizing: border-box;
    text-align: left;
    line-height: 1.6;

    & p:first-child {
        font-family: 'Noto Sans KR', sans-serif;
        font-size: 1.6rem;
        font-weight: 700;
        color: #514862;

        &::before{
            content: '';
            display: block;
            width: 250px;
            height: 250px;
            position: absolute;
            background: #a2a1dc;
            z-index: -1;
            transform: translate(-50%, -25%);
            border-radius: 50%;
            opacity: 0.25;
        }
    }

    & > p:nth-child(2){
        margin-top: 50px;
        font-size: 1.2rem;
        color: #a2a1dc;

        &::after{
            content: '';
            display: block;
            width: 100px;
            height: 100px;
            position: absolute;
            background: #a2a1dc;
            z-index: -1;
            transform: translate(-150%, -65%);
            border-radius: 50%;
            opacity: 1;
        }
    }

    & > p:nth-child(3){
        margin-top: 50px;
        color: #555555;
    }
`

const SkillWraper = styled.div`
     display: flex;
    flex-flow: row wrap;
    flex-basis: 55%;
    padding: 10px;
    box-sizing: border-box;
`
const SkillText = styled.div`
    width: 50%;
    box-sizing: border-box;
`
// http://jsfiddle.net/xwmWK/
// hover before transition
const IntroduceLinkWrapper = styled.div`
    margin-top: 50px;
    position: relative;
    
     & a {
        display: inline-block;
        color: #514862;
        border: 2px solid #514862;
        border-radius: 50px;
        padding: 15px 30px;
        
        text-decoration: none;

        overflow: hidden; 
        position: relative;

        &::before{
            content: '';
            background: #a2a1dc;
            z-index: -1;

            position: absolute;
            top: 0;
            left: 0;
            width: 0px;
            height: 100%;

            transition: all .7s;
            opacity: 0.5;
        }
        &:hover{
            font-weight: 800;

            &::before{
                content: '';
                width: 100%;
                background: #a2a1dc;
            }
        }

        & + a {
            margin-left: 5px;
        }
     }
`;